import btnComponent from '@/components/btnComponent.vue';
export default {
	components: {
		btnComponent
	},
	data() {
		return {
			myClub: null,

			keyword: '',
			page: 1,
			list: [],

			active: 'fansclub_count'
		};
	},
	onShow() {
		this.getMyFansClub()
		this.getList()
	},
	onReachBottom() {
		this.page++
		this.getList()
	},
	onShareAppMessage(e) {
		const shareType = e.target && e.target.dataset.share
		return this.$app.commonShareAppMessage(shareType)
	},
	methods: {
		changeField(field) {
			this.active = field
			this.page = 1
			this.getList()
		},
		setKeyword(e) {
			this.keyword = e.detail.value
			this.page = 1
			this.getList()
		},
		join(item) {
			if (item.star_id != this.$app.getData('userStar').id) {
				this.$app.toast(`请加入${this.$app.getData('userStar').name}的粉丝圈`)
				return
			}

			this.$app.modal(`是否加入${item.clubname}粉丝团`, () => {
				this.$app.request('fans/join', {
					id: item.id
				}, res => {
					this.$app.toast('加入成功！', 'success')
					this.getMyFansClub()
					this.getList()
				}, 'POST', true)
			}, '加入')

		},
		
		getMyFansClub() {
			this.$app.request('fans/info', {}, res => {
				this.myClub = res.data
			})
		},
		getList() {
			this.$app.request('fans/list', {
				keyword: this.keyword,
				page: this.page,
				field: this.active,
			}, res => {
				if (this.page == 1) {
					this.list = res.data
				} else {
					this.list = this.list.concat(res.data)
				}
			})
		}

	}
}
